<template>
    <section class="out-in">
        <!-- gate-nav -->
        <div class="gate">
            <img src="../../assets/imgs/gate-nav-s.jpg" alt="">
            <ul class="gate-nav">
                <li v-for="n in 8" @click="handleClick(n)">
                    <div class="number">{{n}}</div>
                    <div class="place"></div>
                </li>
            </ul>
        </div>
        <el-date-picker class="date-picker"
                        v-model="dateRange"
                        type="daterange"
                        :clearable="false"
                        :editable="false"
                        placeholder=""
                        @change ='getCount'
                        :pickerOptions="pickerOptions">
        </el-date-picker>
        <div class="tip">
            <span>{{chnl_no}}</span>
        </div>
        <div class="charts">
            <div id="chartDate"></div>
        </div>
    </section>
</template>

<script>
    import echarts from 'echarts/lib/echarts'
    // 
    require('echarts/lib/chart/bar')
    // 
    require('echarts/lib/component/tooltip')
    require('echarts/lib/component/title')

    import api from "@/api/index"
    import convertDate from  "@/utils/convertDate"
    export default {
        data() {
            return {
                token: localStorage.getItem("token"),
                counts: {
                    timeList: [],
                    numList: []
                },
                chnl_no: '1',
                dateRange: null,
                chartDate:null,
                pickerOptions: {
                    disabledDate(time) {
                        //  t+1
                        return time > Date.now()
                    },
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                }
            }
        },
        computed:{
            startTime() {
                if (this.dateRange && this.dateRange[0]) {
                    return convertDate(this.dateRange[0])
                }else{
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 6);
                    return convertDate(start)
                }

            },
            endTime() {
                if (this.dateRange && this.dateRange[1]) {
                    return convertDate(this.dateRange[1])
                }else{
                    return convertDate(new Date())
                }
            },
        },
        methods: {
            getCount() {
                let chnl_no = String(this.chnl_no)
                // :  . ,startTime 
                let starttime = this.startTime
                let endtime = this.endTime
                return api.getCount({
                            token: this.token,
                            gate_id: 'A',
                            chnl_no,
                            starttime,
                            endtime
                        }).then((res) => {
                            this.counts.timeList =  res.data.time_list
                            this.counts.numList =  res.data.num_list
                        }).then(() =>{
                            this.updateChart()
                        })
            },
            handleClick(index) {
                this.chnl_no = index
                console.log(index)
                this.getCount()
            },
            updateChart(){
                this.chartDate.setOption({
                    tooltip: {},
                    color: ['#20A0FF'],
                    xAxis: {
                        data: this.counts.timeList
                    },
                    yAxis: {},
                    series: [{
                        name: '',
                        type: 'bar',
                        data: this.counts.numList
                    }]
                })
            }
        },
        watch :{
            counts(val){
                console.log(val)
                this.updateChart()
            }
        },
        created() {
            this.getCount()
            console.log(this.starttime)
        },
        mounted() {
            this.chartDate = echarts.init(document.getElementById('chartDate'))
        },
        beforeDestroy(){
            this.chartDate && this.chartDate.dispose()
        }

    }
</script>

<style scoped lang="scss">
    .out-in {
        width: 100%;
        overflow-x: hidden;
    }
    .gate {
        width: 1376px;
        height: 428px;
        margin: 10px auto;
        position: relative;
        zoom: 0.67;
        img {
            width: 100%;
            height: 100%;
        }
        .gate-nav {
            position: absolute;
            width: 1280px;
            top: 131px;
            left: 50px;
            display: flex;
            flex-direction: row-reverse;
            li {
                width: 104px;
                margin-right: 35px;
                cursor: pointer;
                border: 3px solid #fff;
                .number {
                    height: 54px;
                    background-color: rgba(0, 255, 0, 0.7);
                    line-height: 56px;
                    text-align: center;
                    font-size: 30px;
                }
                .place {
                    height: 205px;
                    background-color: rgba(0, 255, 0, 0.7);
                }
                &:nth-child(5) {
                    margin-right: 200px;
                }
            }
        }
    }
    .date-picker{
        margin: 10px 100px;
    }
    .charts {
        display: flex;
        #chartDate {
            width: 950px;
            height: 400px;
            overflow-x: hidden;
        }
    }
    .tip{
        margin-left: 120px;
        font-size: 20px;
        span{
            color: #20A0FF;
            font-weight: bold;
        }
    }
</style>



