export default function convertDate(DateObj) {
    var dateString = DateObj.toLocaleDateString();
    // 2017525 ipad
    if (dateString.indexOf('') > -1) {
        dateString = dateString.replace('', '-')
        dateString = dateString.replace('', '-')
        dateString = dateString.replace('', '')
        return dateString
    }

    // 5/25/2017 Ubantu
    else if (dateString.split('/')[2] && dateString.split('/')[2].length === 4) {
        return [dateString.split('/')[2], dateString.split('/')[0], dateString.split('/')[1]].join("-")
    }

    // 2017-5-17 Mac
    else if (dateString.split('-')[0] && dateString.split('-')[0].length === 4) {
        return dateString
    }

    // 2017/5/25 windows
    else {
        return dateString.split('/').join("-")
    }
}


